import React, {
	useEffect,
	useState,
	memo,
} from "react";


import {_monthNames, getStore, updStore } from "../stores/Utils";

import "./MonthPicker.css";
import { SvgIcon_Cmpt } from ".";
import ObserveEvent from "../hooks/observeEvent";


const Svg = SvgIcon_Cmpt();

const DropDwnPickerComponent = ({init, onSelect, list, lbl_empty, direction }) => {
	
	const [open, setOpen] = useState(false);
	const [show, setShow] = useState(false);
	const [showCO, setShowCO] = useState(false);


	const handleSelection = (v) => {
		onSelect(v)
		handleClick();
	}



	const handleClick = () => {
		if(show){
			setShow(false)
			setShowCO(false)
			setTimeout(() => {
				setOpen(false);
				
			}, 200);
		}else if(!show){
			setOpen(true)
			setTimeout(() => {
				setShow(true)
			}, 50);
			setTimeout(() => {
				setShowCO(true)
			}, 450);
		}
	};

	

	const handleClickOutSide = () => {
				
		if(open){
			setShow(false);
			setShowCO(false);
			setTimeout(() => {
				setOpen(false);
			}, 200);
		}
		
		
	}


	let labelInit = list.filter(e=>e.id===init)?.[0];

	let dir = direction || "left";

	return (
		<div style={{position:"relative"}}  id={`drpdwn_click_outside`} open={open}>
			<div className={"filter_month_btn _dsplFlx"} onClick={handleClick}>
				<div className="lbl date" >
						{labelInit?.label || lbl_empty}
				</div>
				
					<Svg name={"arrow_drop_down"} color={"var(--hrm_palette-blue-purple)"}/>
				
			</div>
			
			{open?
			 <div className={"OpenBoxContainer"} direction={`${dir}`}>
				<div  className={" monthList"} style={{}}>
					{list.map((elm, wwInd)=>{
						let mnt = elm.label;

						if(!mnt){
							return null;
						}
						return (
							<div key={mnt + "_" +wwInd}  className={"filter_month_item _dsplFlx"} onClick={()=>handleSelection(elm.id)}>
								<div className="lbl tr_bar_10" >
								
								</div>
								<div className="lbl tr_bar_70" >
									{mnt}
								</div>
								<div className="lbl tr_bar_20" >
									{init===elm.id?
									<div>
										<Svg name={"success"} color={"var(--hrm_palette-blue-purple)"}/>
									</div>
									:null}
								</div>
							</div>
						)
					})}
				</div>
			</div>
			:null}
		
			
		</div>
		
	);
};

export default DropDwnPickerComponent;

